import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class UserAvatar extends Component {
    constructor() {
        super();
        this.state = { showModal:false };
    }
    render() {
        const {url, height, width, alt, classes, name} = this.props
        const initalChar = name ? name.charAt(0).toUpperCase() : '';
        return  <>{url ? 
            <img className={classes} src={url} height={height} width={width} alt={alt} /> 
            :
            <span className="symbol-label bg-primary text-light-alt fw-bolder fs-6">{initalChar}</span>}</>
    }
}

export default UserAvatar;